header#header{
  background-color: transparent;
  background-image: none;
}

.green-top-block {
  @include block;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--color-dark);
  color: var(--color-light);
}

.region-header-top-left{
  position: relative;
  padding: 5px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;

  & div{
    margin: 5px;
  }
}

.region-header-top-center{
  padding: 5px;
}

.navigation.menu--topnav ul.menu{
  display: flex;
  gap: 5px;

  & li a{
    color: var(--color-light);
    transition: all 0.4s ease-in-out;

    &:hover{
      color: var(--hover-color);
    }
  }
}

.region-header-top-right{
  max-width: 30%;
  min-width: 200px;
  margin-left: auto;
  padding: 5px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  gap: 5px;
  & .social-link-item{
    display: block;
    position: relative;
    height: 20px;
    width: 20px;
    flex-grow: 0;
    padding: 0;
    overflow: hidden;
    transition: all 0.4s ease-in-out;
    &:hover{
      scale: 1.2;
    }

    & img{
      display: block;
      height: 20px;
      width: 20px;
      object-fit: contain;
      filter: grayscale(100%);

    }

    & .field--name-field-social-link{
    @include visually-hidden;

    }

  }
}
