:root {
  --color-dark: #000;
  --color-light: #fff;
  --hover-color: #0693e3;
}

html {
  font-size: 10px;
}

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}

body {
  font-size: 1.4rem;
  line-height: 1.5;
}

header#header {
  background-color: transparent;
  background-image: none;
}

.green-top-block {
  position: relative;
  max-width: 1100px;
  height: auto;
  padding: 10px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--color-dark);
  color: var(--color-light);
}

.region-header-top-left {
  position: relative;
  padding: 5px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
}
.region-header-top-left div {
  margin: 5px;
}

.region-header-top-center {
  padding: 5px;
}

.navigation.menu--topnav ul.menu {
  display: flex;
  gap: 5px;
}
.navigation.menu--topnav ul.menu li a {
  color: var(--color-light);
  transition: all 0.4s ease-in-out;
}
.navigation.menu--topnav ul.menu li a:hover {
  color: var(--hover-color);
}

.region-header-top-right {
  max-width: 30%;
  min-width: 200px;
  margin-left: auto;
  padding: 5px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  gap: 5px;
}
.region-header-top-right .social-link-item {
  display: block;
  position: relative;
  height: 20px;
  width: 20px;
  flex-grow: 0;
  padding: 0;
  overflow: hidden;
  transition: all 0.4s ease-in-out;
}
.region-header-top-right .social-link-item:hover {
  scale: 1.2;
}
.region-header-top-right .social-link-item img {
  display: block;
  height: 20px;
  width: 20px;
  object-fit: contain;
  filter: grayscale(100%);
}
.region-header-top-right .social-link-item .field--name-field-social-link {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}